import { useMemo } from 'react';
import { useContainer } from 'unstated-next';
import { PluginContainer } from 'core/plugins/hooks';
export const useGetHomeCards = () => {
    const { pluginMap } = useContainer(PluginContainer);
    const cards = useMemo(() => Object.entries(pluginMap).flatMap(([path, { icon, component, cardComponent, displayName }]) => cardComponent
        ? [
            {
                path: component && path,
                Component: cardComponent,
                name: displayName,
                Icon: icon,
            },
        ]
        : []), [pluginMap]);
    return { cards };
};
